import { Component, OnInit } from '@angular/core';
import { Message } from 'primeng/primeng';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { ContactInformation, LinkageTheme } from '../custom-constant/custom-code-value';
import { QueryCommunicationEventResultStatisticsBean } from './bean/custom-contact-statistics.bean';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { FileCenterHttpService } from '../../file-center/http/file-center.http.service';
import { TZB_CUS_DOWNLOAD } from '../../../../../@uisftech/common/service/http.constant';

@Component({
  selector: 'custom-contact-statistics',
  templateUrl: './custom-contact-statistics.component.html',
  styleUrls: ['./custom-contact-statistics.component.scss']
})
export class CustomContactStatisticsComponent implements OnInit {

  msgs: Message[] = [];
  planContactTimeStart: Date = null;
  planContactTimeEnd: Date = null;
  ch: any = CALENDAR_CN;
  contactWay: any;
  hrOrgName: any;
  tableMesg: any;
  total: any;
  first: any = 1;
  pageSize: any;
  showFlagSelect: Boolean = false; // 
  display: boolean = false;
  setInvalue: any = [];
  mapList: any = []; // 
  contactTotalNum: any = 0; // 
  successTotalNum: any = 0; // 
  failTotalNum: any = 0; // 
  depositTotalNum: any = 0; // 
  renewalTotalNum: any = 0; // 
  giftTotalNum: any = 0; // 
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;//
  code: any;
  customContactStatistics: any;
  allList: any; // 
  selectedList: any;// 
  allListTwo: any;// 

  contactInformation: any = ContactInformation // 
  linkageTheme: any = LinkageTheme; // 
  statisticsBean: QueryCommunicationEventResultStatisticsBean = new QueryCommunicationEventResultStatisticsBean();
  personPage: boolean = false;
  userInValue: any;
  rzjg: string;//
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public fileCenterHttpService: FileCenterHttpService,
  ) {
    // this.rzjg = JSON.parse(this.commfunc.getSessionData('commonHeader')).extParam.orgId;
  }

  ngOnInit() {
    this.contactWay = [
      { label: '1', name: "" }
    ]
    // this.setInvalue = [
    //   { label: '', name: "processUserId",},
    //   { label: '', name: "processUserName",},
    //   { label: '', name: "marketingContactWay",},
    //   { label: '', name: "marketingContactTheme",},
    //   { label: '', name: "successTotal",},
    //   { label: '', name: "failTotal",},
    //   { label: '', name: "depositTotal",},
    //   { label: '', name: "renewalTotal",},
    //   { label: '', name: "recoverTotal",},
    //   { label: '', name: "giftTotal",},
    // ]
    this.queryCommunicationEventResultStatistics()

    this.codeVal()
    this.allList = this.code['customContactStatistics'];//
    // this.allList.forEach((el, index) => {
    //   if (el.value == 'processUserId' || el.value == 'processUserName' || el.value == 'marketingContactWay' || el.value == 'marketingContactTheme') {
    //     this.allList.splice(index, 1);
    //   }
    // });
    // 
    for (let i=this.allList.length-1; i>=0; i--) {
      if (this.allList[i].value == 'processUserId' || this.allList[i].value == 'processUserName' || this.allList[i].value == 'marketingContactWay' || this.allList[i].value == 'marketingContactTheme') {
        this.allList.splice(i, 1);
      }
    }

    let arr1 = this.allList
    console.log(arr1)
    this.allList.forEach(element => {
      if (element.value == "processUserId") {
        element['index'] = 1
      } else if (element.value == "processUserName") {
        element['index'] = 2
      } else if (element.value == "processDepartmentName") {
        element['index'] = 3
      } else if (element.value == "processDepartmentId") {
        element['index'] = 4
      } else if (element.value == "processSubbranchName") {
        element['index'] = 5
      } else if (element.value == "processBranchId") {
        element['index'] = 6
      } else if (element.value == "processBranchName") {
        element['index'] = 7
      } else if (element.value == "processSubbranchId") {
        element['index'] = 8
      } else if (element.value == "marketingContactWay") {
        element['index'] = 9
      } else if (element.value == "marketingContactTheme") {
        element['index'] = 10
      } else if (element.value == "successTotal") {
        element['index'] = 11
      } else if (element.value == "failTotal") {
        element['index'] = 12
      } else if (element.value == "contactTotal") {
        element['index'] = 13
      } else if (element.value == "giftTotal") {
        element['index'] = 14
      } else if (element.value == "depositTotal") {
        element['index'] = 15
      } else if (element.value == "recoverTotal") {
        element['index'] = 16
      } else if (element.value == "renewalTotal") {
        element['index'] = 17
      } 
    });
    let arr2 = this.allList
    console.log(arr2)
    for (let i = 0; i < this.allList.length; i++) {
      for (let j = i + 1; j < this.allList.length; j++) {
        let tmp;
        if(this.allList[i].index > this.allList[j].index) {
          tmp = this.allList[j];
          this.allList[j] = this.allList[i];
          this.allList[i] = tmp;
        }
      }
    }

    // this.partition(this.allList, 0, this.allList.length-1)
    let arr3 = this.allList
    console.log(arr3)
    // 
    this.allList.forEach(element => {
      if (element.value !== "processBranchId" && element.value !== "processBranchName"
        && element.value !== "processSubbranchId" && element.value !== "processDepartmentId"
        && element.value !== "processSubbranchName" && element.value !== "processDepartmentName") {
        // if (element.label == '' || element.label == '') {
        //   this.setInvalue.unshift(element)
        // } else {
        this.setInvalue.push(element)
        // }
      }

    });
    this.selectedList = this.setInvalue
  }


  //
  codeVal() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    console.log(ret)
    return this.code = ret //code
  }

  // 
  queryCommunicationEventResultStatistics() {
    let param = this.statisticsBean
    this.httpService.queryCommunicationEventResultStatistics(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.mapList = data.resultList;
        this.total = data.totalNum;
        this.contactTotalNum = data.contactTotal  // 
        this.successTotalNum = data.successTotal  // 
        this.failTotalNum = data.failTotal  // 
        this.depositTotalNum = data.despositAmount  // 
        this.renewalTotalNum = data.continueAmount  // 
        this.giftTotalNum = data.recoverAmount // 
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  queryCommunicationEventResultExport() {
    let param = {
      userName: JSON.parse(this.commfunc.getSessionDataCH('chName')).userName
    }
    this.httpService.queryCommunicationEventResultExport(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        window.open(this.DOWNLOAD + "?fileName=" + encodeURI(data.fileName) + "&fileUrl=" + data.fileUrl, "_self")
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  reset() {
    this.statisticsBean = new QueryCommunicationEventResultStatisticsBean();
    this.statisticsBean.pageSize = 10;
    this.statisticsBean.pageNum = 1;
    this.first = 0;
    this.queryCommunicationEventResultStatistics();
  }

  //
  paginate(event) {
    this.statisticsBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.statisticsBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.statisticsBean.pageSize;
    this.queryCommunicationEventResultStatistics();
  }

  //
  chooseWorker() {
    document.body.style.overflow = 'hidden';
    this.personPage = !this.personPage;
    this.userInValue = this.rzjg;
  }

  //
  perInfor(item) {
    document.body.style.overflow = 'auto';
    this.personPage = false;
    if (item.length > 0) {
      this.statisticsBean.processUserId = item[0].tellerId;
      this.statisticsBean.hrOrgName = item[0].tellerName;
    }
  }

  // 
  addUserDefinedSet(event) {
    console.log(event)
    this.display = event.display;
    if (event && event.selectedList && event.selectedList.length > 0) {
      this.setInvalue = event.selectedList;
      for (let i = 0; i < this.setInvalue.length; i++) {
        for (let j = i + 1; j < this.setInvalue.length; j++) {
          let tmp;
          if(this.setInvalue[i].index > this.setInvalue[j].index) {
            tmp = this.setInvalue[j];
            this.setInvalue[j] = this.setInvalue[i];
            this.setInvalue[i] = tmp;
          }
        }
      }
    }
  }

  // 
  selectFeil() {
    this.display = true;
    this.selectedList = this.setInvalue
  }

  // 
  handInputDate(param1, param2) {
  }

  // 
  showTime() {
    let flag = this.commfunc.judgeexpireDate(this.statisticsBean.contactTimeStart, this.statisticsBean.contactTimeEnd)
    console.log(flag)
    if (flag) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
      this.statisticsBean.contactTimeEnd = null;
    }
  }

}
