import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { ContactInformation, LinkageTheme, PriorityQuery, CustomerSource, IsContact, LinkageThemeTwo } from '../../custom-constant/custom-code-value';
import { CALENDAR_CN } from '../../../../../../@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { DatePipe } from '@angular/common';
import { FormGroup, FormBuilder, FormControl, Validators } from "@angular/forms";
@Component({
  selector: 'custom-contact-list-update',
  templateUrl: './custom-contact-list-update.component.html',
  styleUrls: ['./custom-contact-list-update.component.scss']
})
export class CustomContactListUpdateComponent implements OnInit {

  @Input() inValue: any;
  @Output() outValue = new EventEmitter();

  contactWay: any;
  deployTimeEnd: any;
  deployTimeStart: any;
  msgs: any;
  ch: any = CALENDAR_CN;
  dataObj: any;
  setInValue: any = {
    timeName: '',
  }

  contactInformation: any = ContactInformation // 
  linkageTheme: any = LinkageThemeTwo; // 
  triority: any = PriorityQuery; // 
  customerSource: any = CustomerSource; // 
  isContact: any = IsContact; // 
  superior: boolean = false; //
  userform: FormGroup; //
  custName: string = ''; //
  custNo: string = ''; //
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public datePipe: DatePipe,
    public fb: FormBuilder,
  ) { }

  ngOnInit() {
    this.userform = this.fb.group({
      'custId': new FormControl(''),
      "custName": new FormControl(''),
      "marketingContactTheme": new FormControl('', [Validators.required]),
      // "isContact": new FormControl('', [Validators.required]),
      "marketingContactWay": new FormControl('', [Validators.required]),
      "remark": new FormControl('', [Validators.required]),
      "priority": new FormControl('', [Validators.required]),
      "contactRetime": new FormControl(''),
      "contactResult": new FormControl(''),
    });
  }
  ngOnChanges() {
    console.log(this.inValue)
    this.custNo = this.inValue.custId;
    this.custName = this.inValue.custName;
    if (this.inValue.contactSource == '01') {
      this.superior = true;
    } else {
      this.superior = false;
    }
    this.dataObj = this.inValue;
    this.dataObj.isContact = "1";
    if (typeof this.dataObj.marketingContactTheme == "string") {
      this.dataObj.marketingContactTheme = this.dataObj.marketingContactTheme.split(",")
    }
    this.dataObj.fromDate = this.datePipe.transform(this.dataObj.fromDate, 'yyyy-MM-dd HH:mm');
    this.historyContent()
    this.selectCustBriefInfo()
    // this.dataObj.fromDate = new Date(this.dataObj.fromDate);
    // console.log(this.dataObj.fromDate)
    this.initTime()
  }

  // 
  initTime() {
    // let time = new Date();
    // time = this.commfunc.DateAdd('d', 1, time, 'add');
    // let a = this.datePipe.transform(time, 'yyyy-MM-dd');
    // this.dataObj.fromDate = a + " 10:00:00";
    this.setInValue = {
      timeName: '',
      date: this.dataObj.fromDate
    }
  }

  // 
  getOutValue(event) {
    console.log(event)
    this.dataObj.fromDate = event.time
    // this.taskBean.marMonitoringTimeTo =  event.time;
  }

  // 
  reset() {
    this.outValue.emit({ display: false, code: true });
  }

  // 
  updateCommunicationEventByMyself() {
    // if (this.dataObj.marketingContactTheme.length == 1 || !this.dataObj.marketingContactTheme) {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'info', summary: '', detail: '' });
    //   return;
    // }
    if (this.dataObj.marketingContactTheme) {
      this.dataObj.marketingContactTheme = this.dataObj.marketingContactTheme.join();
    }
    this.dataObj.fromDate = this.datePipe.transform(this.dataObj.fromDate, 'yyyy-MM-dd HH:mm');
    let param = this.dataObj
    this.httpService.updateCommunicationEventByMyself(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.outValue.emit({ display: false, code: true, message: '' })
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  selectCustBriefInfo() {
    let param = {
      custNo: this.dataObj.custId
    }
    this.httpService.selectCustBriefInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataObj['managerId'] = data.principalLinkman; // 
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  historyContent() {
    let param = {
      custId: this.custNo,
      custName: this.custName
    }
    this.httpService.checkCommunicationEventResultInfo(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.dataObj['lastTimeContact'] = data.lastTimeContact;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  contactButton() {
    let param = {
      custName: this.custName
    }
    let session = window.sessionStorage;//by zj   session
    let bussinessWhere = null;
    bussinessWhere = '(query_Indexa=' + "'" + this.custNo + "'" + ' or ' + 'query_Indexb=' + "'" + this.custNo + "'" + ' or '
      + 'query_Indexc=' + "'" + this.custNo + "'" + ' or ' + 'query_Indexd=' + "'" + this.custNo + "'" + ')'
    session.setItem('bussinessWhere', bussinessWhere);
    window.open('#/pages/tzb/custom/reminder-do/personal-do;custName=' + this.custName + ";custNo=" + this.custNo + ";show=3", '_blank')
  }

   // 
   multSelectMax() {
    console.log(this.dataObj.marketingContactTheme);
    if (this.dataObj.marketingContactTheme.length > 3) {
      this.dataObj.marketingContactTheme.splice(this.dataObj.marketingContactTheme.length - 1, 1)
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "3" });
      return;
    } else {
      this.dataObj.contactList = [];
      this.dataObj.marketingContactTheme.forEach(element => {
        let obj = {
          marketingContactTheme: '', //
          contactTime: '', //	
          contactResult: '0', //	
          contactRemark: '', //	
          contactAmount: '', //	
          contactGift: '1', //	
          contactGiftRemark: '', //	
        }
        obj.marketingContactTheme = element
        this.dataObj.contactList.push(obj);
      });

    }

  }

}
