import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { ContactStatus, HistoryProcessingResults, TourcesPlanning, LinkageTheme, ContactInformation, ProcessingStarts, ProcessingResults } from '../custom-constant/custom-code-value';
import { CustomContactHistoryBean } from './bean/custom-contact-history.bean';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { CALENDAR_CN } from '../../../../../@uisftech/common/constant/config';
import { DatePipe } from '@angular/common';
import { ViewChild } from '@angular/core';
import { Dialog } from 'primeng/primeng';
@Component({
    selector: 'custom-contact-history',
    templateUrl: './custom-contact-history.component.html',
    styleUrls: ['./custom-contact-history.component.scss'],
    providers: [DatePipe]
})
/**
 * 
 */
export class CustomContactHistoryComponent implements OnInit {

    mapList: any = []
    display: boolean = false;
    hrOrgName: any;
    contactWay: any;
    deployTimeEnd: any;
    ch: any = CALENDAR_CN;
    deployTimeStart: any;
    total: any;
    tableMesg: any;
    first: any = 1;
    pageSize: any;
    msgs: any;
    inValue: any; // 

    contactInformation: any = ContactInformation; //
    linkageTheme: any = LinkageTheme; //
    tourcesPlanning: any = TourcesPlanning; //
    processingResults: any = ProcessingResults; //
    processingStarts: any = HistoryProcessingResults; //

    historyBean: CustomContactHistoryBean = new CustomContactHistoryBean();

    // 
    @ViewChild('dialog') dialog: Dialog;
    
    constructor(
        public httpService: CusOperationHttpService,
        public commfunc: Commfunc,
        public datePipe: DatePipe,
    ) { }

    ngOnInit() {
        this.query();
    }
    query() {
        this.historyBean.pageSize = 10;
        this.historyBean.pageNum = 1;
        this.first = 0;
        this.queryPorcessedCommunicationEvent();
    }
    queryPorcessedCommunicationEvent() {
        this.historyBean.contactTimeStart = this.datePipe.transform(this.historyBean.contactTimeStart, 'yyyy-MM-dd');
        this.historyBean.contactTimeEnd = this.datePipe.transform(this.historyBean.contactTimeEnd, 'yyyy-MM-dd');
        let param = this.historyBean;
        this.httpService.queryPorcessedCommunicationEvent(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.mapList = data.resultList
                this.total = data.totalNum
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }

    //
    tocust(col) {
        this.commfunc.setSessionData('custNo', col.custId); // by zj
        window.open('#/pages/custom-view/general', '_blank')
    }

    // 
    reset() {
        this.historyBean = new CustomContactHistoryBean();
        this.query();
    }

    // 
    resrut(event) {
        this.display = event.dispaly
    }

    paginate(event) {
        this.historyBean.pageSize = event.rows * 1;
        let currentPage = event.page + 1;
        this.historyBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.historyBean.pageSize;
        this.queryPorcessedCommunicationEvent();
    }

    showTime() {
        let flag = this.commfunc.judgeexpireDate(this.historyBean.contactTimeStart, this.historyBean.contactTimeEnd)
        console.log(flag)
        if (flag) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
            this.historyBean.contactTimeEnd = null;
        }

    }

    handInputDate(deploy, start) {

    }

    details(col) {
        this.inValue = col;
        this.display = true;
    }

    ngAfterViewInit() {
        this.cancelDialogShake();
    }

    // 
    cancelDialogShake() {
        if (this.dialog) {
            this.dialog.center = function () {
                let elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                let elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                if (elementWidth === 0 && elementHeight === 0) {
                    this.containerViewChild.nativeElement.style.visibility = 'hidden';
                    this.containerViewChild.nativeElement.style.display = 'block';
                    elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                    elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                    this.containerViewChild.nativeElement.style.display = 'none';
                    this.containerViewChild.nativeElement.style.visibility = 'visible';
                }
                let viewport = this.domHandler.getViewport();
                let x = Math.max(Math.ceil((viewport.width - elementWidth)) / 2, 0);
                let y = Math.max(Math.ceil((viewport.height - elementHeight)) / 2, 0);
                // 
                if (!this.containerViewChild.nativeElement.style.left ||
                    !this.containerViewChild.nativeElement.style.top ||
                    Math.abs(this.containerViewChild.nativeElement.style.left - x) > 5 ||
                    Math.abs(this.containerViewChild.nativeElement.style.top - y) > 5
                ) {
                    this.containerViewChild.nativeElement.style.left = x + 'px';
                    this.containerViewChild.nativeElement.style.top = y + 'px';
                }
            };
        }
    }
}
