import { Component, OnInit, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Input } from '@angular/core';
import { CustomContactHistoryDetaileean } from '../bean/custom-contact-history.bean';
import { Priority, LinkageTheme, ContactInformation, CustomerSource, ProcessingResults } from '../../custom-constant/custom-code-value';
import { Output } from '@angular/core';
import { EventEmitter } from '@angular/core';

@Component({
    selector: 'custom-contact-history-details',
    templateUrl: './custom-contact-history-details.component.html',
    styleUrls: ['./custom-contact-history-details.component.scss']
})
/**
 * 
 */
export class CustomContactHistoryDetailsComponent implements OnInit, OnChanges {

    @Input() inValue: any;
    @Output() outValue = new EventEmitter();

    mapList: any = [
        { coreOrgName: '1' }
    ]
    deployTimeEnd: any;
    ch: any;
    deployTimeStart: any;
    valList: any;
    msgs: any;
    showFlagSubmit: boolean = true;

    customerSource: any = CustomerSource; //
    contactInformation: any = ContactInformation; //
    linkageTheme: any = LinkageTheme; //
    priority: any = Priority; //
    processingResults: any = ProcessingResults; //
    showContractNum: boolean = false;

    detaileean: CustomContactHistoryDetaileean = new CustomContactHistoryDetaileean()

    constructor(
        public httpService: CusOperationHttpService,
    ) { }

    ngOnInit() {
        console.log(this.inValue);

    }

    ngOnChanges() {
        console.log(this.inValue);
        this.query(this.inValue)
    }

    // 
    reset() {
        this.outValue.emit({ dispaly: false })
    }

    query(col) {
        let param = {
            // workEffortId: col.communicationEventId, // 
            communicationEventId: col.communicationEventId, // 
        };
        this.httpService.queryConWorkEffortDetailInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.detaileean = data;
                //  02 11 12 14
                if (this.detaileean.marketingContactTheme == '02' || this.detaileean.marketingContactTheme == '11' || this.detaileean.marketingContactTheme == '12' || this.detaileean.marketingContactTheme == '14') {
                    this.showContractNum = true;
                }
                if (typeof this.detaileean.marketingContactTheme == "string" && this.detaileean.marketingContactTheme) {
                    this.detaileean['marketingContactTheme_'] = this.detaileean.marketingContactTheme.split(",")
                }else if(!this.detaileean.marketingContactTheme){
                    this.detaileean['marketingContactTheme_'] = ''
                }
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
    }
}
