import { Component, OnInit } from '@angular/core';
import { Message, ConfirmationService } from 'primeng/primeng';
import { CALENDAR_CN } from '../../credit-ccm/common/http/credit-ccm.http.constant';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from '../../../../../@uisftech/common/service/commonFun';
import { ContactInformation, LinkageTheme, TaskStatusQuery, Priority, TourcesPlanning, PriorityQuery, StatusId2 } from '../custom-constant/custom-code-value';
import { QueryUnprocessedCommunicationEventBean } from './bean/custom-contact-list.bean';
import { DatePipe } from '@angular/common';
import { ViewChild } from '@angular/core';
import { Dialog } from 'primeng/components/dialog/dialog';
@Component({
  selector: 'custom-contact-list',
  templateUrl: './custom-contact-list.component.html',
  styleUrls: ['./custom-contact-list.component.scss'],
  providers: [DatePipe]
})
export class CustomContactListComponent implements OnInit {

  dialogHeader: any;
  mapList: any = [
    { coreOrgName: '123' }
  ]
  msgs: Message[] = [];
  ch: any = CALENDAR_CN;
  display: boolean = false;
  showFlagDetails: boolean = false; // 
  showFlagUpdate: boolean = false; // 
  showFlagHandle: boolean = false; // 
  showFlagHandle1: boolean = false; // 
  session: any; // 
  tableMesg: any; // 
  total: any; // 
  first: any = 1; // 
  pageSize: any; // 
  inValueDetaile: any; // 
  inValueUpdate: any; // 
  inValueHandle: any; // 

  contactInformation: any = ContactInformation // 
  linkageTheme: any = LinkageTheme; // 
  taskStatus: any = StatusId2; // 
  triority: any = PriorityQuery; // 
  tourcesPlanning: any = TourcesPlanning; // 
  userId: any; // id
  personPage: boolean = false; // 
  setValue: any = {};
  eventBean: QueryUnprocessedCommunicationEventBean = new QueryUnprocessedCommunicationEventBean()

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public confirmationService: ConfirmationService,
    public datePipe: DatePipe,
  ) { }

  // 
  @ViewChild('dialog') dialog: Dialog;

  ngOnInit() {
    // id
    this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId
    // 
    this.getUserName();
    this.eventBean.partyIdFrom = this.userId;
    this.eventBean.partyNameFrom = this.session.userName;
    this.queryUnprocessedCommunicationEvent()
  }

  // 
  getUserName() {
    this.session = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    console.log(this.session);
  }

  paginate(event) {
    this.eventBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.eventBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.eventBean.pageSize;
    this.queryUnprocessedCommunicationEvent();
  }

  // 
  detailsReturn(event) {
    this.display = event.display;
    this.showFlagDetails = event.display;
    this.showFlagUpdate = event.display;
    console.log(event.message)
    if (event.message) {
      this.msgs = [];
      this.msgs.push({ severity: 'success', summary: '', detail: event.message });
    }
    this.queryUnprocessedCommunicationEvent();
  }

  // 
  handleReturn(event) {
    console.log(event);
    this.display = event.display;
    this.showFlagHandle = event.display;
    if (event.personPage) {
      this.personPage = event.personPage;
      this.setValue = {
        custId: event.custId,
        custName: event.custName,
        contactSource: "02",
        // lastTimeContact: this.lastTimeContact
      }
    }else{
      this.queryUnprocessedCommunicationEvent();      
    }

  }
    // 
    addAustom(event) {
      // this.personPage = event.display;
      if (event.display == false) {
        if (event.sign == 'save') {
          this.msgs = [];
          this.msgs.push({ severity: 'success', summary: '', detail: "" });
          setTimeout(() => {
            this.personPage = false;
          })
        } else {
          this.personPage = false;
        }
      }
      this.queryUnprocessedCommunicationEvent();
    }

  // 
  reset() {
    this.eventBean = new QueryUnprocessedCommunicationEventBean();
    this.eventBean.pageSize = 10;
    this.eventBean.pageNum = 1;
    this.first = 0;
    this.eventBean.partyIdFrom = this.userId;
    this.eventBean.partyNameFrom = this.session.userName;
    this.queryUnprocessedCommunicationEvent();
  }

  // 
  query() {
    this.eventBean.pageSize = 10;
    this.eventBean.pageNum = 1;
    this.first = 0;
    this.queryUnprocessedCommunicationEvent();
  }

  // 
  queryUnprocessedCommunicationEvent() {
    this.eventBean.fromDateStart = this.datePipe.transform(this.eventBean.fromDateStart, 'yyyy-MM-dd');
    this.eventBean.fromDateEnd = this.datePipe.transform(this.eventBean.fromDateEnd, 'yyyy-MM-dd');
    let param = this.eventBean
    this.httpService.queryUnprocessedCommunicationEvent(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.mapList = data.resultList;
        this.total = data.totalNum
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  delete(col) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        let param = {
          communicationEventId: col.communicationEventId,// ID
        };
        this.httpService.deleteCommunicationEventByMyself(param).subscribe(data => {
          if (data.returnCode.code == 'success') {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: "" });
            this.queryUnprocessedCommunicationEvent()
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }
  //  
  onHideClose() {
    this.showFlagHandle = false;
    this.showFlagUpdate = false;
    this.showFlagDetails = false;
    this.queryUnprocessedCommunicationEvent();
  }

  // 
  handle(col) {
    this.display = true;
    this.dialogHeader = '';
    this.showFlagHandle = true;
    this.showFlagUpdate = false;
    this.showFlagDetails = false;

    this.inValueHandle = col;
  }

  // 
  upDate(col) {
    let param = col;
    this.display = true;
    this.dialogHeader = '';
    this.showFlagUpdate = true;
    this.showFlagDetails = false;
    this.showFlagHandle = false;
    this.inValueUpdate = col;
  }

  // 
  details(col) {
    console.log(col);
    this.display = true;
    this.dialogHeader = '';
    this.showFlagDetails = true;
    this.showFlagUpdate = false;
    this.showFlagHandle = false;

    this.inValueDetaile = col
  }

  //
  tocust(col) {
    this.commfunc.setSessionData('custNo', col.custId); // by zj
    window.open('#/pages/custom-view/general', '_blank')
  }

  // 
  handInputDate() {
    console.log(123)
    this.showTime();
  }

  // 
  showTime() {
    let flag = this.commfunc.judgeexpireDate(this.eventBean.fromDateStart, this.eventBean.fromDateEnd)
    console.log(flag)
    if (flag) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
      this.eventBean.fromDateEnd = null;
    }
  }

  // 
  cancelDialogShake() {
    if (this.dialog) {
        this.dialog.center = function () {
            let elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
            let elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
            if (elementWidth === 0 && elementHeight === 0) {
                this.containerViewChild.nativeElement.style.visibility = 'hidden';
                this.containerViewChild.nativeElement.style.display = 'block';
                elementWidth = this.domHandler.getOuterWidth(this.containerViewChild.nativeElement);
                elementHeight = this.domHandler.getOuterHeight(this.containerViewChild.nativeElement);
                this.containerViewChild.nativeElement.style.display = 'none';
                this.containerViewChild.nativeElement.style.visibility = 'visible';
            }
            let viewport = this.domHandler.getViewport();
            let x = Math.max(Math.ceil((viewport.width - elementWidth)) / 2, 0);
            let y = Math.max(Math.ceil((viewport.height - elementHeight)) / 2, 0);
            // 
            if (!this.containerViewChild.nativeElement.style.left ||
                !this.containerViewChild.nativeElement.style.top ||
                Math.abs(this.containerViewChild.nativeElement.style.left - x) > 5 ||
                Math.abs(this.containerViewChild.nativeElement.style.top - y) > 5
            ) {
                this.containerViewChild.nativeElement.style.left = x + 'px';
                this.containerViewChild.nativeElement.style.top = y + 'px';
            }
        };
    }
}

}
