import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CustomContactApprovalBean } from './bean/custom-contact-approval.bean';
import { CusOperationHttpService } from '../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { CustomerSource, LinkageTheme, ContactInformation, PushType, Priority } from '../custom-constant/custom-code-value';
import { Router } from '@angular/router';

@Component({
  selector: 'custom-contact-approval',
  templateUrl: './custom-contact-approval.component.html',
  styleUrls: ['./custom-contact-approval.component.scss']
})
export class CustomContactApprovalComponent implements OnInit {

  detailList: any = [];
  marketingCampaignId: string; // 
  msgs: Message[] = [];
  dataMap: any = {};
  obj: any = {};
  isShow: any = {};
  detailDisplay: boolean = false;
  detailInValue: any; // 

  customerSource: any = CustomerSource; // 
  linkageTheme: any = LinkageTheme; //
  contactInformation: any = ContactInformation; //
  pushType: any = PushType; //
  priority: any = Priority; //
  ontactApproval: any = { // 
    code: true
  }

  approvalBean: CustomContactApprovalBean = new CustomContactApprovalBean()

  constructor(
    public routeInfo: ActivatedRoute,
    public httpService: CusOperationHttpService,
    public router: Router,
  ) { }

  ngOnInit() {

    this.detailList = this.routeInfo.snapshot.params;
    console.log(this.detailList);
    if (this.detailList.detailFlag) {
      this.ontactApproval = { // 
        code: true,
        detailFlag: this.detailList.detailFlag //  1  2
      }
    }
    this.dataMap = JSON.parse(this.detailList.dataMap)
    console.log(this.dataMap);
    this.marketingCampaignId = this.dataMap.marketingCampaignId
    console.log(this.marketingCampaignId);

    this.codeVal()
    this.queryMarketingCampaignDetail();
  }

  // 
  queryMarketingCampaignDetail() {
    let param = {
      marketingCampaignId: this.marketingCampaignId
    }
    this.httpService.queryMarketingCampaignDetail(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.obj = data
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

  // 
  detailCall(event) {

  }

  // 
  pushClick() {
    this.detailDisplay = true;
    this.detailInValue = {
      marketingCampaignId: this.marketingCampaignId
    }
    // this.router.navigate(['/pages/tzb/custom/custom-contact-new/custom-contact-task/custom-speed-track',{marketingCampaignId: this.marketingCampaignId}])
  }

  //
  codeVal() {
    let param = {
      // paramGroupId: "COP",
      // description: "",
      paramName: "custContactPostName",
      // paramTypeId: "business",
      // paramId: "196000003200000",
      // paramValue: "custContactPostName",
      // status: "start",
      // limit: "10",
    }
    this.httpService.queryCommonParamItemService(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        data.returnCommonParamItemList.forEach(element => {
          this.pushType.push({
            label: element.paramItemValue,
            value: element.paramItemName
          })
        });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    })
  }

}
